@import '/src/assets/styles/variables.scss';

.header {
  display: flex;
  justify-content: center;
  background-color: $primary-light-3;
  width: 100%;
  height: 4em;
  font-size: 1.25em;
  color: $secondary-dark-1;
  filter: drop-shadow(0px -10px 40px rgba(0, 0, 0, 0.25));

  .container {
    display: flex;
    height: 100%;
    justify-content: space-between;
    align-items: center;
    margin: 0 1em;

    .services {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .service {
        margin: 0 0.5em;

        &:first-child {
          margin-left: 0 !important;
        }
      }

      
    }

    .meta {
      display: flex;
      align-items: center;

      .name {
        margin: 0 1em;
      }

      .logout {
        cursor: pointer;
      }
    }
  }

  svg {
    fill: $primary-dark-1;
    width: $svg-size;
    height: $svg-size;
    margin: 0;
  }
}
