@import '/src/assets/styles/variables.scss';

.card {
  display: inline-block;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.25);
  width: 200px;

  &:hover {
    box-shadow: 0 0 20px 5px rgba(0, 0, 0, 0.5);

  }

  .upper {
    background-color: $primary-dark-1;
    height: 200px;
    display: flex;
    justify-content: center;
    align-items: center;



    .outerCircle {
      background-color: $primary-dark-2;
      border-radius: 50%;
      height: 50%;
      width: 50%;
      display: flex;
      align-items: center;
      justify-content: center;

      .innerCircle {
        background-color: $primary-light-1;
        border-radius: 50%;
        height: 50%;
        width: 50%;
        display: flex;
        justify-content: center;
        align-items: center;

        svg {
          fill: $primary-dark-1;
          width: $svg-size;
          height: $svg-size;
          margin: auto 0.5em;
        }
      }
    }
  }

  .lower {
    font-size: 1.25em;
    height: 2.5em;
    line-height: 2.5em;
    background-color: $secondary-light-1;
    color: $secondary-dark-1;
    text-align: center;
  }
}
