@import '/src/assets/styles/variables.scss';

.wrapper {
  position: relative;
  width: 200px;
  font-size: 1em;
  user-select: none;
  background-color: $primary-light-3;

  button {
    overflow: hidden;
    width: inherit;
    margin: inherit;
    padding: inherit;
    border: none;
    background: inherit;
    font: inherit;
    line-height: normal;
    color: inherit;
    text-align: inherit;
    -webkit-appearance: none;
  }

  & > button {
    border: 1px solid $primary-dark-4;
  }
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    line-height: 2em;
    cursor: pointer;

    .headerTitle {
      margin: 2px 20px;
    }

    svg {
      margin-right: 20px;
      fill: $secondary-dark-1;
      max-width: 20px;
    }
  }

  .list {
    position: absolute;
    overflow: hidden;
    z-index: 10;
    width: 100%;
    max-height: 215px;
    border-top: none;
    border: 1px solid $primary-dark-4;
    border-top: none;
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    background-color: $primary-light-3;
    text-align: left;
    -webkit-overflow-scrolling: touch;

    .listItem {
      display: inline-block;
      margin-left: 10px;
      width: 100%;
      padding: 8px 10px;
      line-height: 1.6rem;
      white-space: nowrap;
      text-overflow: ellipsis;
      cursor: default;
      cursor: pointer;

      &:last-child {
        border-bottom: none;
      }
    }
  }
}
