@import "/src/assets/styles/variables.scss";

.card {
  display: inline-flex;
  padding: 2em;
  flex-direction: column;
  box-shadow: -5px -5px 100px 10px rgba(0, 0, 0, 0.25), 5px 5px 100px 10px rgba(0, 0, 0, 0.25);
  border-radius: 1em;
}

.dark {
  background-color: $primary-dark-1;
}

.light {
  background-color: $primary-light-1;
} 